/********************************
 * 1. Font Declarations
 *********************************/
@font-face {
  font-family: 'Glacial Indifference';
  src: local("Glacial Indifference"), url("../assets/fonts/GlacialIndifference-Regular.otf") format("opentype"); }

/***************************************
 * 2. Element Overrides for Browser Defaults
 ***************************************/
/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box;
  height: 100%; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  color: #1c3749; }

#root {
  height: 100%; }

a,
a span {
  color: #1c3749;
  text-decoration: none; }

button {
  background: #fff;
  border: none; }

img {
  max-width: 100%;
  max-height: 100%; }

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #1c3749;
  margin: 8px 0;
  font-weight: 400;
  line-height: 1; }
  h1 span,
  h2 span,
  h3 span,
  h4 span,
  h5 span,
  h6 span {
    display: block; }
  h1.subheading,
  h2.subheading,
  h3.subheading,
  h4.subheading,
  h5.subheading,
  h6.subheading {
    color: #1c4c6c;
    font-weight: bold;
    text-transform: uppercase; }

h2 {
  letter-spacing: 0px; }

p {
  margin: 16px 0; }

/************************
 * 3. Typography
 ************************/
.text-size-xxs {
  font-size: 12px !important; }

.text-size-xs {
  font-size: 16px !important; }

.text-size-s {
  font-size: 24px !important; }

.text-size-m {
  font-size: 32px !important; }

.text-size-l {
  font-size: 48px !important; }

.text-size-xl {
  font-size: 72px !important; }

.text-color-light {
  color: #1c4c6c; }

.text-color-dark {
  color: #1c3749; }

/************************
 * 4. Global Layout
 **************************/
.page {
  position: relative;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0;
  display: flex;
  min-height: 0;
  flex-direction: column;
  overflow: hidden; }

@media only screen and (min-width: 480px) {
  .page .top-container h1,
  .page .top-container h2 {
    max-width: 100%;
    text-align: center; } }

@media only screen and (min-width: 1024px) {
  .page .top-container.bezel-l {
    margin-top: 16px; } }

/*****************************************
 * 5. UI Component Styling
 ****************************************/
.scroll-list {
  position: relative;
  display: flex;
  min-height: 0;
  flex-direction: column;
  z-index: 1;
  list-style-type: none;
  margin: 0;
  padding: 16px 16px;
  overflow-y: auto;
  flex: 0 1 auto; }

.scroll-gradient-bottom .scroll-list li:last-child::after {
  content: '';
  height: 20px;
  width: 100%;
  display: inline-block; }

.scroll-gradient-top:before {
  content: '';
  width: 102%;
  left: -1%;
  height: 32px;
  position: absolute;
  z-index: 100;
  top: 0;
  -webkit-box-shadow: inset 0px 10px 5px 0px #95b6e5;
  -moz-box-shadow: inset 0px 10px 5px 0px #95b6e5;
  box-shadow: inset 0px 10px 5px 0px #68a6d6;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,95b7e5+99 */
  /*background: -moz-linear-gradient(bottom, rgba(255, 255, 255, 0) 0%, rgba(104, 166, 214, 1) 99%);
    /* FF3.6-15 */
  /*background: -webkit-linear-gradient(bottom,
        rgba(255, 255, 255, 0) 0%,
        rgba(104, 166, 214, 1) 75%);
    /* Chrome10-25,Safari5.1-6 */
  /*background: linear-gradient(to top,
        rgba(255, 255, 255, 0) 0%,
        rgba(104, 166, 214, 1) 75%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */ }

.scroll-gradient-bottom:after {
  content: '';
  width: 102%;
  left: -1%;
  height: 64px;
  position: absolute;
  z-index: 100;
  bottom: -16px;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+0,95b7e5+99 */
  background: -moz-linear-gradient(top, rgba(255, 255, 255, 0) 0%, #95b7e5 99%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, #68a6d6 75%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #68a6d6 75%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */ }

@media only screen and (min-width: 1024px) {
  .scroll-gradient-top:before,
  .scroll-gradient-bottom:after {
    display: none; } }

.speech-bubble {
  position: relative;
  display: inline-block;
  background-color: #fff;
  border-radius: 12px;
  padding: 32px; }
  .speech-bubble:after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border-top: 24px solid #fff;
    border-right: 36px solid transparent; }
  .speech-bubble.reversed:after {
    border-right: none;
    border-left: 36px solid transparent; }
  .speech-bubble.decorative {
    height: 120px;
    width: 140px; }

/*
 * 6. UI Component Style accents
 */
/* Stars */
.star {
  position: absolute;
  display: inline-block;
  width: 0;
  height: 0;
  z-index: 10;
  margin-left: 0.9em;
  margin-right: 0.9em;
  margin-bottom: 1.2em;
  border-right: 0.3em solid transparent;
  border-bottom: 0.7em solid #fff;
  border-left: 0.3em solid transparent;
  /* Controlls the size of the stars. */
  font-size: 12px; }
  .star:before, .star:after {
    content: '';
    display: block;
    width: 0;
    height: 0;
    position: absolute;
    top: 0.6em;
    left: -1em;
    border-right: 1em solid transparent;
    border-bottom: 0.7em solid #fff;
    border-left: 1em solid transparent;
    transform: rotate(-35deg); }
  .star:after {
    transform: rotate(35deg); }

/* Bezel borders */
.bezel-xs {
  background-color: #fff;
  border-top: 2px solid #eee;
  border-left: 2px solid #e5e5e5;
  border-bottom: 2px solid #ccc;
  border-right: 2px solid #ddd; }

.bezel-s {
  background-color: #fff;
  border-top: 4px solid #eee;
  border-left: 4px solid #e5e5e5;
  border-bottom: 4px solid #ccc;
  border-right: 4px solid #ddd; }

.bezel-m {
  background-color: #fff;
  border-top: 8px solid #eee;
  border-left: 8px solid #e5e5e5;
  border-bottom: 8px solid #ccc;
  border-right: 8px solid #ddd; }

.bezel-l {
  background-color: #fff;
  border-top: 16px solid #eee;
  border-left: 16px solid #e5e5e5;
  border-bottom: 16px solid #ccc;
  border-right: 16px solid #ddd; }

/* Circles */
.circle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 50%;
  background-color: #fff;
  line-height: 1;
  color: #1c4c6c; }
  .circle p {
    margin: 0;
    align-self: center; }
  .circle:first-line {
    color: #1c3749; }
  .circle p:nth-of-type(2) {
    margin-top: 8px;
    font-size: 16px; }

.circle-l {
  width: 262px;
  height: 262px;
  font-size: 40px; }

.circle-m {
  width: 112px;
  height: 112px;
  font-size: 40px; }

.circle-s {
  width: 55px;
  height: 55px;
  font-size: 30px; }

/* Rectangles */
.rectangle {
  background-color: #fff;
  border-radius: 8px; }

/* drop-shadows */
.drop-shadow {
  z-index: 10;
  filter: drop-shadow(3px 3px 3px #68a6d6); }

/*********************************
 * 7. Animations and Transitions
 *********************************/
@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in-delayed {
  0% {
    opacity: 0; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fade-in-and-grow {
  from {
    opacity: 0;
    transform: scale(0); }
  to {
    opacity: 1;
    transform: scale(1); } }

@keyframes fade-in-and-twirl {
  0% {
    opacity: 0;
    filter: none;
    transform: rotate(-760deg); }
  50% {
    opacity: 1;
    filter: none;
    transform: rotate(0deg); }
  100% {
    opacity: 1;
    filter: none;
    filter: drop-shadow(0px 0px 3px #68a6d6); } }

@keyframes fly-in-right {
  0% {
    transform: translateX(100vw); }
  100% {
    transform: translateX(0); } }

@keyframes fly-in-right-with-shadow {
  0% {
    transform: translateX(100vw);
    filter: none; }
  60% {
    transform: translateX(0);
    filter: none; }
  80% {
    transform: translateX(0);
    filter: drop-shadow(3px 3px 3px #68a6d6); }
  100% {
    transform: translateX(0);
    filter: drop-shadow(3px 3px 3px #68a6d6); } }

@keyframes fly-in-bottom {
  0% {
    transform: translateY(100vh); }
  50% {
    transform: translateY(0); }
  100% {
    transform: translateY(0); } }

@keyframes fly-in-bottom-with-shadow {
  0% {
    transform: translateY(100vh);
    filter: none; }
  50% {
    transform: translateY(0);
    filter: none; }
  100% {
    transform: translateY(0);
    filter: drop-shadow(3px 3px 3px #68a6d6); } }

@keyframes pulsate {
  0% {
    transform: scale(1); }
  50% {
    transform: scale(1.1); }
  100% {
    transform: scale(1); } }

@keyframes twinkle {
  0% {
    transform: scale(1) translateY(0); }
  40% {
    transform: scale(1) translateY(0); }
  50% {
    transform: scale(1.3) translateY(-25%); }
  60% {
    transform: scale(1) translateY(0); }
  100% {
    transform: scale(1) translateY(0); } }
