body.covid-conversation {
  /**
    * Page specific styling
  */ }
  body.covid-conversation .page.welcome {
    background-image: url("../../assets/images/covid-conversation-background.png");
    background-repeat: no-repeat;
    background-position-x: 100%;
    background-position-y: 50%;
    background-size: 600px; }
    body.covid-conversation .page.welcome .top-container {
      width: 50%;
      min-width: 550px;
      padding-top: 48px;
      background-color: rgba(255, 255, 255, 0.8); }
      body.covid-conversation .page.welcome .top-container .title-image {
        max-width: 100%; }
    body.covid-conversation .page.welcome .welcome-quote {
      width: 50%;
      min-width: 550px;
      padding: 50px 20px 20px 100px;
      background-color: rgba(255, 255, 255, 0.8); }
      body.covid-conversation .page.welcome .welcome-quote h2 {
        font-size: 48px;
        line-height: 64px;
        color: #000;
        letter-spacing: -1px; }
        body.covid-conversation .page.welcome .welcome-quote h2 span {
          display: inline;
          color: #064b6e; }
      body.covid-conversation .page.welcome .welcome-quote p {
        font-family: 'Glacial Indifference'; }
    body.covid-conversation .page.welcome a {
      margin-left: 100px; }
      body.covid-conversation .page.welcome a button {
        margin-top: 40px; }
    body.covid-conversation .page.welcome .bottom-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      background: none;
      padding-top: 50px;
      background-color: transparent; }
      body.covid-conversation .page.welcome .bottom-container a,
      body.covid-conversation .page.welcome .bottom-container div {
        min-width: 100px;
        margin-left: 0; }
      body.covid-conversation .page.welcome .bottom-container a.donate-cta-link {
        min-width: 250px; }
      body.covid-conversation .page.welcome .bottom-container img {
        width: 50px; }
    body.covid-conversation .page.welcome .donate-cta-link {
      display: flex;
      flex-direction: row;
      align-items: center;
      font-size: 24px; }
      body.covid-conversation .page.welcome .donate-cta-link img {
        vertical-align: bottom; }
      body.covid-conversation .page.welcome .donate-cta-link span {
        height: auto;
        font-size: 16px; }
    body.covid-conversation .page.welcome .map-cta {
