      animation-iteration-count: infinite; }
    @media only screen and (max-width: 768px) {
      body.covid-conversation .page.welcome {
        min-height: 100%;
        background-image: url("../../assets/images/covid-conversation-welcome-bottom.png");
        background-repeat: no-repeat;
        background-position-y: 105%;
        background-size: 100%; }
        body.covid-conversation .page.welcome .top-container {
          width: 100%;
          min-width: 100%;
          padding: 20px; }
        body.covid-conversation .page.welcome .welcome-quote {
          width: 100%;
          min-width: 100%;
          padding: 20px 60px 0 60px; }
          body.covid-conversation .page.welcome .welcome-quote h2 {
            font-size: 40px;
            line-height: 50px; }
        body.covid-conversation .page.welcome a {
          margin-left: 60px; }
          body.covid-conversation .page.welcome a button {
